<script>
	import { createEventDispatcher } from 'svelte';
	let active = false;
	export let text = '';
	export let sendDispatch;
	
	const dispatch = createEventDispatcher();
	
	function handleDispatch(e) {
		active = !active;
		dispatch('message',{text: e.sendDispatch, enabled: active});
	}
	
	
</script>

<style>
	li {
		list-style-type: none;
		cursor: pointer;
		position: relative;
		padding: 12px 8px 12px 40px;
		background: #eee;
		font-size: 25px;
		transition: 0.2s;

		/* make the list items unselectable */
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	}

	li:nth-child(odd) {
		background: #f9f9f9;
	}

	li:hover {
		background: #ddd;
	}

	li.active {
		background: #fc6837;
		color: #fff;
	}
</style>

<!-- <li class:active="{active}" on:click="{() => {active = !active;}}">{text}</li> -->
<li class:active="{active}" on:click="{() => {handleDispatch({sendDispatch});}}">{text}</li>
	
